    position: absolute;
    top: 18px;
    left: 15px;
    border-radius: 20px; }
  [data-toggle="buttons"] .form-check-label.active:before {
    background: #509ff6; }
  [data-toggle="buttons"] .form-check-label.active:after {
    content: '';
    width: 8px;
    height: 8px;
    position: absolute;
    top: 24px;
    left: 21px;
    border-radius: 8px;
    background: #fff; }

.spinner {
