    height: 20px;
    top: 50%;
    left: 0;
    background-color: #fff;
    border: 1px solid #ddd;
    transform: translateY(-50%); }
  .spinner:after {
    top: 13px;
    left: 2.66667px;
    background-color: #21c56f;
    height: 4px;
    width: 4px;
    animation: spin 1s linear infinite;
    transform-origin: 7.33333px; }

@keyframes spin {
